.content {
	width: 90%;
	margin: 0 25px;
	height: 100%;
	padding-bottom: 20px;
}

.table_holder {
	background-color: #191b1f;
	margin-top: 40px;
	padding: 15px;
	border-radius: 20px;
}

.table_holder table {
	text-align: center;
	/* width: 100%; */

	width: 100%;
	display: table;
	border-spacing: 0;
	border-collapse: collapse;
}

thead {
	display: table-header-group;
	vertical-align: middle;
}

tr {
	color: inherit;
	display: table-row;
	outline: 0;
	vertical-align: middle;
	border-top: none;
}

th,
td {
	color: #fff;
	border: none;
	font-size: 12px;
	font-family: inherit;
	padding-block: 6px;
	background-color: #191b1f;
	width: 30px;
}

tbody {
	display: table-row-group;
	vertical-align: middle;
}
